<template>
  <div class="p-4 lg:w-1/2">
    <div
      class="h-full flex sm:flex-row flex-col items-center sm:justify-start justify-center text-center sm:text-left"
    >
      <img
        alt="team"
        class="flex-shrink-0 rounded-lg w-48 h-48 object-cover object-center sm:mb-0 mb-4"
        v-bind:src="`${imageurl}`"
        loading="lazy"
      />
      <div class="flex-grow sm:pl-8">
        <h2 class="title-font font-medium text-lg text-gray-900">{{projectTitle}}</h2>
        <h3 class="text-gray-500 mb-3">{{projectType}}</h3>
        <p class="mb-4 text-justify">{{projectDescription}}</p>
        <span class="inline-flex">
          <a class="text-indigo-500" v-bind:href="application" id="applink">Application</a>
          <a class="ml-2 text-indigo-500" v-bind:href="`${githublink}`">GitHub</a>
        </span>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "ProjectBody",
  props: {
    projectTitle: String,
    projectType: String,
    projectDescription: String,
    applicationlink: String,
    githublink: String,
    imageurl: String
  },
  computed: {
    application() {
      return this.applicationlink || null;
    }
  }
};
</script>